import React from "react";
import Emoji from "./Emoji";
import Tech from "./Tech";

import "./styles/Tools.css"
import "./styles/Projects.css"

export default function () {
    const [isVisible, setVisible] = React.useState(false);
    const domRef = React.useRef<HTMLHeadingElement>(null);
  
    React.useEffect(() => {
      const observer = new IntersectionObserver(entries => {
        entries.forEach(entry => {
          
          // In your case there's only one element to observe:     
          if (entry.isIntersecting) {
            
            // Not possible to set it back to false like this:
            setVisible(true);
            
            // No need to keep observing:
            if(domRef.current !== null){
              observer.unobserve(domRef.current);
            }
          }
        });
      });
        if(domRef.current !== null){
          observer.observe(domRef.current);
        }
    }, []);

  return (
    <div className={`toolsContainer fade-in-projects ${isVisible ? 'is-visible' : ''}`} ref={domRef}>
      <h3 className="toolsHeader">
        <Emoji symbol="ðŸ§°" label="Toolbox" /> My toolbox
      </h3>
      <div className="toolLists">
        <div className="toolSection">
          <h2 className="toolListHeader">Front-end</h2>
          <ul className="toolList">
            <Tech name="TypeScript" img="/img/ts-logo-128.svg" alt="TypeScript" />
            <Tech name="React.js" img="/img/React.svg" alt="React.svg" />
            <Tech name="Bootstrap" img="/img/bootstrap-logo.svg" alt="Bootstrap" />
            <Tech name="JavaScript" img="/img/js-logo2.png" alt="JS" />

          </ul>
        </div>
        <div className="toolSection">
          <h2 className="toolListHeader">Back-end</h2>
          <ul className="toolList">
            <Tech name="Node.js/Express" img="/img/node.png" alt="Node.js" />
            <Tech name="Flask" img="/img/flask.png" alt="Flask" />
            <Tech name="MongoDB" img="/img/MongoDB.svg" alt="MongoDB"/>
            <Tech name="MySQL" img="/img/mysql-logo.png" alt="MySql" />
            <Tech name="MS SQL Server" img="/img/sql-server.svg" alt="SQL Server" />
          </ul>
        </div>
        <div className="toolSection">
          <h2 className="toolListHeader">Other Languages</h2>
          <ul className="toolList">
            <Tech name="Python" img="/img/python.png" alt="python" />
            <Tech name="C#" img="/img/CSharp.png" alt="C#" />
            <Tech name="Java" img="/img/java.png" alt="Java" />
          </ul>
        </div>
        <div className="toolSection">
          <h2 className="toolListHeader">Utilities</h2>
          <ul className="toolList">
            <Tech name="Wordpress" img="/img/Wordpress-Logo.svg" alt="Wordpress" />
            <Tech name="Linux Servers" img="/img/tux.png" alt="Linux Servers" />
            <Tech name="Windows Servers" img="/img/windows-server.svg" alt="Windows Servers" />
            <Tech name="Docker" img="/img/Docker.png" alt="Docker" />
          </ul>
        </div>
      </div>
    </div>
  );
}
