import React from "react";
import Emoji from "./Emoji";
import "./styles/About.css";

export default function About() {
  const [isVisible, setVisible] = React.useState(false);
  const domRef = React.useRef<HTMLDivElement>(null);

  React.useEffect(() => {
    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        // In your case there's only one element to observe:
        if (entry.isIntersecting) {
          // Not possible to set it back to false like this:
          setVisible(true);

          // No need to keep observing:
          if (domRef.current !== null) {
            observer.unobserve(domRef.current);
          }
        }
      });
    });
    if (domRef.current !== null) {
      observer.observe(domRef.current);
    }
  }, []);

  return (
    <div
      id="about"
      className={`aboutContainer fade-in-about ${
        isVisible ? "is-visible" : ""
      }`}
    >
      <div ref={domRef} className="aboutInner">
        <img src="/img/lassi_koykka-min.jpg" alt="Me" className={`picOfMe`} />
        <h2 className={`aboutHeader`}>
          <Emoji symbol="ðŸ“" label="pen and paper" /> About me
        </h2>
        <a href="/documents/Lassi_Koykka_CV.pdf">
          <button className="downloadButton">
            {" "}
            <Emoji symbol="ðŸ“„" label="document" />{" "}
            <span style={{ textDecoration: "underline" }}>View my CV</span>
          </button>
        </a>

        <div className={`aboutText`}>
          <p>
            I'm a dedicated and self-motivated developer hobbyist and
            ICT-student originally from Tampere Finland.
          </p>
          <p>
          I'm comfortable working with both back- as well as front-end technologies and my main tools of the trade are 
          <strong> TypeScript, React, Node.js, and Python</strong>, but I like to switch things up when I can and I'm not locked into any specific framework or language.
          </p>
          <p>
            {" "}
            I'm a quick learner and I wish to understand how things actually
            operate under the hood. I thoroughly enjoy the process of creative
            problem-solving and challenging myself by setting goals and
            restrictions. On my spare time, I often like to work on all kinds of
            personal projects, from APIs to websites, games and desktop applications.
            One of my favourite pastimes is playing around with new technologies and
            seeing what I can come up with. I have built all kinds of fun little projects over the past 
            few years and you can find all of them on my <a href="https://github.com/Lassi-Koykka">GitHub account</a>. :)
          </p>
          <p>
            I currently reside in Kuopio and study information technology at
            Savonia University of Applied Sciences.
          </p>
        </div>
      </div>
    </div>
  );
}
